import React, { useState } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
  useLocation,
  RouteProps
} from 'react-router-dom';

import AuthService from './services/AuthService';
import SignIn from './components/SignIn';
import Dashboard from './containers/Dashboard';

const App: React.FC = () => {
  const [authService] = useState(new AuthService());

  return (
    <Router>
      <Switch>
        <HomeRoute authService={authService} path={`${process.env.PUBLIC_URL}/`} exact>
          <h1>Loading...</h1>
        </HomeRoute>
        <SignInRoute authService={authService} path={`${process.env.PUBLIC_URL}/signIn`}>
          <SignIn authService={authService} />
        </SignInRoute>
        <DashboardRoute authService={authService} path={`${process.env.PUBLIC_URL}/dashboard`}>
          <Dashboard />
        </DashboardRoute>
      </Switch>
    </Router>
  );
};

interface PrivateRouteProps extends RouteProps {
  authService: AuthService;
}

const HomeRoute: React.FC<PrivateRouteProps> = ({ authService, children, ...rest }) => {
  const history = useHistory();
  const location = useLocation();
  const { from } = location.state || { from: { pathname: `${process.env.PUBLIC_URL}/` } };

  const authenticate = (): void => {
    authService
      .authenticate()
      .then(() => {
        history.replace(from);
      })
      .catch(() => {
        history.push(`${process.env.PUBLIC_URL}/signIn`, { from: location });
      });
  };

  return (
    <Route
      {...rest}
      render={({ location }) => {
        if (authService.isAuthenticated) {
          return (
            <Redirect
              to={{
                pathname: `${process.env.PUBLIC_URL}/dashboard`,
                state: { from: location }
              }}
            />
          );
        } else {
          authenticate();
          return children;
        }
      }}
    />
  );
};

const SignInRoute: React.FC<PrivateRouteProps> = ({ authService, children, ...rest }) => {
  const history = useHistory();
  const location = useLocation();
  const { from } = location.state || { from: { pathname: `${process.env.PUBLIC_URL}/` } };

  const authenticate = (): void => {
    authService
      .authenticate()
      .then(() => {
        history.replace(from);
      })
      .catch(() => {
        console.log('Unauthenticated');
      });
  };

  return (
    <Route
      {...rest}
      render={({ location }) => {
        if (authService.isAuthenticated) {
          return (
            <Redirect
              to={{
                pathname: `${process.env.PUBLIC_URL}/dashboard`,
                state: { from: location }
              }}
            />
          );
        } else {
          authenticate();
          return children;
        }
      }}
    />
  );
};

const DashboardRoute: React.FC<PrivateRouteProps> = ({ authService, children, ...rest }) => {
  return (
    <Route
      {...rest}
      render={({ location }) =>
        authService.isAuthenticated ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: `${process.env.PUBLIC_URL}/signIn`,
              state: { from: location }
            }}
          />
        )
      }
    />
  );
};

export default App;
