import React, { FormEvent, useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import HourglassEmptyIcon from '@material-ui/icons/HourglassEmpty';

import AuthService from '../services/AuthService';
import Copyright from './Copyright';

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white
    }
  },
  card: {
    minWidth: 275
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
}));

interface SignInProps {
  authService: AuthService;
}

const SignIn: React.FC<SignInProps> = ({ authService }) => {
  const classes = useStyles();
  const [isLoginError, setIsLoginError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const location = useLocation();
  const { from } = location.state || { from: { pathname: `${process.env.PUBLIC_URL}/` } };

  const onSubmitLogin = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setIsLoading(true);
    const data = new FormData(event.currentTarget);
    const username: string = String(data.get('username'));
    const password: string = String(data.get('password'));
    authService
      .signIn(username, password)
      .then(() => {
        setIsLoading(false);
        history.replace(from);
      })
      .catch(() => {
        setIsLoading(false);
        setIsLoginError(true);
      });
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} onSubmit={onSubmitLogin}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Username"
            name="username"
            autoComplete="username"
            autoFocus
            onChange={() => setIsLoginError(false)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={() => setIsLoginError(false)}
          />
          <FormControlLabel control={<Checkbox value="remember" color="primary" />} label="Remember me" />
          {isLoginError && (
            <Card className={classes.card}>
              <CardContent>
                <Typography variant="body1" component="p" color="error">
                  Incorrect username and / or password.
                </Typography>
              </CardContent>
            </Card>
          )}
          {isLoading ? (
            <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit} disabled>
              <HourglassEmptyIcon />
            </Button>
          ) : (
            <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit}>
              Sign In
            </Button>
          )}
        </form>
      </div>
      <Copyright />
    </Container>
  );
};

export default SignIn;
