import axios, { AxiosRequestConfig, AxiosResponse } from 'axios';

class AuthService {
  private _isAuthenticated: boolean;
  public get isAuthenticated(): boolean {
    return this._isAuthenticated;
  }

  constructor() {
    this._isAuthenticated = false;
  }

  public authenticate(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      if (this._isAuthenticated) resolve();

      let token: string = '';
      try {
        token = String(localStorage.getItem('token'));
      } catch (e) {
        console.error('Local Storage not supported');
      }

      if (!token) {
        this.removeAuth();
        reject();
      }

      const url = `${process.env.REACT_APP_API_BASE_URL}${process.env.REACT_APP_API_AUTH}`;
      const options: AxiosRequestConfig = {
        method: 'GET',
        url: url,
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json;charset=UTF-8',
          Authorization: `Bearer ${token}`
        }
      };
      axios(options)
        .then((response: AxiosResponse<any>) => {
          const responseOK = response && response.status === 200;
          if (responseOK) {
            this.addAuth();
            resolve();
          } else {
            this.removeAuth();
            reject();
          }
        })
        .catch(() => {
          this.removeAuth();
          reject();
        });
    });
  }

  public signIn(username: string, password: string): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      const url = `${process.env.REACT_APP_API_BASE_URL}${process.env.REACT_APP_API_LOGIN}`;
      const options: AxiosRequestConfig = {
        method: 'POST',
        url: url,
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json;charset=UTF-8'
        },
        data: {
          username,
          password
        }
      };
      axios(options)
        .then((response: AxiosResponse<any>) => {
          const responseOK = response && response.status === 201;
          if (responseOK) {
            this.addAuth(response.data.token);
            resolve();
          } else {
            this.removeAuth();
            reject();
          }
        })
        .catch(() => {
          this.removeAuth();
          reject();
        });
    });
  }

  public signOut(): Promise<void> {
    return new Promise<void>(resolve => {
      this.removeAuth();
      resolve();
    });
  }

  private addAuth(token?: string): void {
    if (token) {
      try {
        localStorage.setItem('token', token);
      } catch (e) {
        console.error('Local Storage not supported');
      }
    }
    this._isAuthenticated = true;
  }

  private removeAuth(): void {
    try {
      localStorage.removeItem('token');
    } catch (e) {
      console.error('Local Storage not supported');
    }
    this._isAuthenticated = false;
  }
}

export default AuthService;
